<template><div class="content"><h1 class="post-title">行为伦理<a class="header-anchor" id="%E8%A1%8C%E4%B8%BA%E4%BC%A6%E7%90%86" href="#%E8%A1%8C%E4%B8%BA%E4%BC%A6%E7%90%86">#</a></h1><div class="metabar"><div class="metabar-item">2022/10/01</div><div class="metabar-item">思想</div><div class="metabar-item">约 15 分钟读完</div></div>
<p>过去的资本主义世界，尤其是当时工业化的欧洲，最终并没有如马克思与恩格斯的理论所预料的那样发生社会主义革命。意大利共产党创始人之一 Gramsci 注意到了这一点，认为工人之所以失去了反抗力，是因为在某种因素的潜移默化中形成了<em>虚假的意识</em>。针对这一现象，他总结并提出了<strong>文化霸权理论</strong>（<em>egemonia curtuale</em>, cultural hegemony）。</p>
<p>对于文化霸权理论，其具体表现形式，在英文维基百科上被概括为：</p>
<blockquote>
<p>In Marxist philosophy, <strong>cultural hegemony</strong> is the dominance of a <strong>culturally diverse society</strong> by the ruling class who manipulate the culture of that society—the beliefs and explanations, perceptions, values, and mores—so that <strong>the worldview of the ruling class</strong> becomes the accepted cultural norm. As the universal <strong>dominant</strong> ideology, the ruling-class worldview misrepresents the social, political, and economic <em>status quo</em> as <strong>natural, inevitable, and perpetual</strong> social conditions that benefit every social class, rather than as <strong>artificial</strong> social constructs that benefit only the ruling class.</p>
</blockquote>
<p>意译译文：</p>
<blockquote>
<p>在马克思主义哲学中，<strong>文化霸权</strong>是指，统治阶级控制<strong>具有文化多样性的社会的文化</strong>（包括信仰、解释、认知、价值观等），使得<strong>统治阶级的世界观</strong>成为被广泛认可的社会规范。统治阶级将他们的世界观定为社会中普遍且<strong>首要</strong>的意识形态，并将其造成的社会、政治、经济情况解释为一种<strong>自然、必然、会长久持续下去</strong>的社会状况，而且可以使各个阶级都受益，然而实际上这只是为了使统治阶级受益而进行的<strong>蓄意</strong>行为。</p>
</blockquote>
<p>即使后世有人<sup class="footnote-ref"><a href="#fn1" id="fnref1">[1]</a></sup>对这一理论进行批判，认定其似乎将统治阶级摆放在一个过于决定性的位置，忽视了其它不同势力（不一定是阶级）之间显然也存在的斗争，但是依照历史来解读，这一理论似乎在现实中具备完全意义上的可行性。</p>
<p>在本篇文章中所要详尽探讨的并非文化霸权，笔者亦认为没有探讨的必要；实际上以上只是对本文所要阐述的具体现象的一个引子。</p>
<h2>归因<em>行为</em>所遇到的难题<a class="header-anchor" id="%E5%BD%92%E5%9B%A0%E8%A1%8C%E4%B8%BA%E6%89%80%E9%81%87%E5%88%B0%E7%9A%84%E9%9A%BE%E9%A2%98" href="#%E5%BD%92%E5%9B%A0%E8%A1%8C%E4%B8%BA%E6%89%80%E9%81%87%E5%88%B0%E7%9A%84%E9%9A%BE%E9%A2%98">#</a></h2>
<p>道德哲学不可能有统一的结论。这句话好似废话，正如「人与人有不同的观点」。人们在意识形态方面思想差异的形成不是一天两天的事情，似乎也具有一定的必然性；而令我百思不得其解的是，究竟是什么促成了针对这方面思想的一种莫名崇拜，以至于在未深刻感知甚至了解时就已经陶醉其中。</p>
<p>在这样的情况下对一个人与这样的思想有关的行为<sup class="footnote-ref"><a href="#fn2" id="fnref2">[2]</a></sup>进行归因，似乎会有许多矛盾发生。如果认定是由于类似于文化霸权的「思想渗透」，难免也太不负责任，因为实际上这种思想是对意识形态极为粗糙的主观感知，完全不能代表意识形态本身，更不能代表其实际影响，因为这样的主观感知在人群中没有统一性可言；而如果认定只是因为主观感知而进行的自我感动，或者类似民族主义，似乎也轻视了这一主观感知对象的作用——如此大的感染力，使得人愿意哪怕以最粗糙的主观感知去相信。</p>
<p>上述似乎概括了归因它所遇到的两大难题，但不幸的是，一些极端的例子（见下文）指出人的主观感知水平只能维持在这里所指的「最粗糙」的程度，所以这些行为的形成在某种程度上又是一种对其必然性的利用。</p>
<h2>标准<a class="header-anchor" id="%E6%A0%87%E5%87%86" href="#%E6%A0%87%E5%87%86">#</a></h2>
<p>「西方个人主义是否在侵蚀我国 00 后一代？」</p>
<p>这是一个知乎上的<a href="https://www.zhihu.com/question/431664810" class="external-link" target="_blank" rel="noopener noreferrer">问题<span class="external-link-icon mdi mdi-launch"></span></a>。其中<a href="https://www.zhihu.com/question/431664810/answer/2558737254" class="external-link" target="_blank" rel="noopener noreferrer">一篇回答<span class="external-link-icon mdi mdi-launch"></span></a>认为，当<strong>标准</strong>飘忽不定时，一切讨论的结果会被最终的解释所掩盖，因而许多问题都是没有意义的。这是很浅显的道理，却也显得振聋发聩。</p>
<p>其中的一段话是：</p>
<blockquote>
<p>什么是00后？是指广义上出生于公元2000年以后的一代人？还是他们中含着金钥匙出生，享受着顶级资源的那一部分？还是面对社会现实茫然无措，上不来下不去的那部分？还是买不起学区房，请不起家教，走不通关系，最后被分流到中职的那一部分人？还是毕业即失业，但因从未就业，不被算作失业人群，无法领取失业保险的那一部分人？还是即使知道房地产前景黯淡，但依然为了有个安身立命的所在，掏空家底，背上负债的那批人？</p>
</blockquote>
<p>实际上，在我们日常的闲谈亦或者宏大叙事中，很容易提到某一代人的概念，但直到现在，我都没有彻底想通，这样的陈述究竟是在针对这一代人表达什么。</p>
<p>也许早先人们发现一群人的共同特征后，会将称他们为一个统一的名字；而当这样的人在各地普遍存在时，这一统一的名字就正式成为了一个不统一的概念。</p>
<p>闲谈之中的模糊指代不负责任很正常，也许只是当地的事情，却可能被发言积极分子映射到所有人，但显然没人在意或影响不大；而当这样的模糊进入了文章中，甚至作为了具体的抨击对象，就显得有些不伦不类了。</p>
<p>除此之外，还有许多种界定不清，可以阅读这篇回答了解。</p>
<p>这种现象的存在，似乎也是一种必然，因为人们显然没有时间去了解这些概念的具体定义，并且对这些定义做出合理的选择和注释，从而提出严谨的问题。在这里又得出了一个问题：</p>
<p><strong>究竟是什么让人们愿意在自己对概念与现象了解不清晰甚至错误时，使用许多自己了解不甚清楚的词汇去构造一个确是传达了某种观念却又模糊不清的问题呢？</strong></p>
<p>答案也许是：去证明<strong>自己</strong>思想观念的正确性。这就好比当你做对了一道很难很难的数学题目，就迫不及待想要和同伴分享一样。自己也许对问题的了解不甚深入，却完全可以以这样日常闲谈的风格发出，引起许多人的长篇大论，各执一词。</p>
<p>但就跟「为什么」骗局一样，许多时候在为什么之后所跟的并不是事实，却往往就这样被误导为事实。</p>
<p>依我看来，若是把这样的问题当成真正的问题，哪怕最为精辟的论述恐怕也会成为无的放矢的笑谈。所以专注的学者和理性的观众往往对其嗤之以鼻，只是大概率无法被看到。</p>
<p>总地来说，当今许多媒体上，无论是由官方还是个人所发布的言论，相当大的一部分充斥着模糊不清的语言和概念，却依然能够被一部分人接受，一部分人反驳，好比是在针对假命题反复斟酌，最终得出「这一命题<strong>部分</strong>错误」的结论。</p>
<p>这是互联网信息爆炸的必然结果，群体对精准定义的弱化也是不求严格表达的媒体存在的基石。闲谈也是一样的道理，没有营养，但有快感，大家都喜欢。</p>
<h2>自我机械化<a class="header-anchor" id="%E8%87%AA%E6%88%91%E6%9C%BA%E6%A2%B0%E5%8C%96" href="#%E8%87%AA%E6%88%91%E6%9C%BA%E6%A2%B0%E5%8C%96">#</a></h2>
<p>闲谈有什么错？闲谈没有错。</p>
<p>然而有许多思想不完备之人，急于用滥调填补自己思想任务的空缺。</p>
<p>无论是出于某种信仰，还是自幼的熏陶，这样的人往往赋予了自己某种思想任务，需要通过不断主动地对上文所提及的「闲谈」式碎片化知识进行主观的吸收，从而作为某种填充物填充内心里因思想任务而形成的空缺。</p>
<p>而这份空缺的填满，就能增进自己对于身处某一集体的认同。</p>
<p>当过分地吸收碎片化消息，不加以反思、查证或者整理（这三者均属于泛指，并非学习的行为）时，思维很容易形成一种「应当及时将其应用」的错觉。久而久之，这或将成为你评判事物的固定标准。而且可以推断，越是囿于这一标准，越是容易接受与之类似的信息，最终形成极其单一的价值观。但可笑的是，它们都不来源于自己。</p>
<p>此即自我机械化，并非任何外界主体的作用，而是个人<strong>主动地被</strong>媒体环境塑造。</p>
<p>保持一个清醒、独立思考的头脑十分重要。它是个人观点形成的基础。若是被碎片化信息侵蚀，很难摆脱。笔者认为，长此以往，即使是最终凭借自己的经验去思考问题，也难以脱离碎片化信息长期造成的思维框架；甚至许多人从此认定碎片化信息塑造的框架就是唯一正确的，只因它与其所在的集体思想不谋而合。</p>
<h2>行为伦理<a class="header-anchor" id="%E8%A1%8C%E4%B8%BA%E4%BC%A6%E7%90%86" href="#%E8%A1%8C%E4%B8%BA%E4%BC%A6%E7%90%86">#</a></h2>
<p>什么是行为伦理？因不明原因所形成的对行为道德的界定行为。一个直到现在还是有争议的问题：同性恋是否合<strong>理</strong>？可以有以下几种解释：</p>
<ul>
<li><strong>唯物唯心主义</strong>：普遍见到的动植物都需要雌雄结合才能繁衍后代；生物存活的目的就是繁衍后代；同性恋违背了自然界的法则。</li>
<li><strong>唯心唯物主义 1</strong>：同性恋是一种心理疾病，经过治疗以后应该就能好；所有的同性恋应该接受这样的治疗，从而回归正常的生活。</li>
<li><strong>唯心唯物主义 2.1</strong>：同性恋会导致艾滋病等性病的大范围传播，对社会有负面影响，且最开始的艾滋病和某些性病就是由人不正常的性偏好所导致的。</li>
<li><strong>唯心唯物主义 2.2</strong>：同性恋是艾滋病等性病的大范围传播的原因，对社会有负面影响，且最开始的艾滋病和某些性病就是由人不正常的性偏好所导致的。</li>
<li><strong>完全唯心主义</strong>：同性恋就是变态；看到同性恋就是反胃；这是本能反应，没有为什么。</li>
<li><strong>理想主义</strong>：人人都是平等的，都具有爱自己爱的人的权利；人人都可以为自己的爱而抗争；这样的抗争是必要且值得的。</li>
<li><strong>极端主义</strong>：同性恋应该都去死。</li>
<li><strong>神秘主义</strong>：与我无关；不支持；不反对。</li>
</ul>
<p>如此繁多的解释使得一个问题具有巨大的讨论空间。一个人若是处于理想的思想状态，针对上述所有的解释，大都可以推断出其形成原因，是为换位思考，而并不会产生对于某一种较为强烈的排斥情绪，但也不会改变自己的立场；而若是处于自我机械化的思想状态，那么利用单一的价值标准，就会极力的排斥其它观点，对其他观点感到极为不适。</p>
<p>那么当或健康或歪曲的价值标准被应用到行为伦理的领域中时，就会产生人与人之间思想以外的碰撞。对同性恋的抵抗或者支持也属于这一范围。</p>
<h2>总结<a class="header-anchor" id="%E6%80%BB%E7%BB%93" href="#%E6%80%BB%E7%BB%93">#</a></h2>
<p>最初写这篇文章的动机在于了解到这样的人的存在以后，内心无法填补的疑惑。将这篇文章作为这类人的纪念。</p>
<p>同时，往往这样的人用陈词滥调彰显自己在这方面不明含义的「博学」气质的时候，显露出前所未有的自恋嘴脸，同时却看不起那些明面上表达自己与众不同观点的人（以积极的方式），认为他们才是「自恋」，批判起来也是滔滔不绝。因而在我看来，这样的行为根本不能算作是过度的自我规训，反倒是一种彻头彻尾的虚伪，是应该被唾弃的。</p>
